import React, { useState } from "react";
// import { useState } from 'react';
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import ImageForm from "../Assests/Checklist.jpg";
import { SnackbarProvider, enqueueSnackbar } from 'notistack';


function Form() {
  const [userdata, setuserdata] = useState({
    fname: "",
    lname: "",
    company: "",
    email: "",
    message: "",
  });


  
  
  const getValue = (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setuserdata({ ...userdata, [name]: value });
  };
  const sendRequest = async (event) => {
    event.preventDefault();
    const { fname, lname, company, email, message } = userdata;
if(fname && lname && company && email && message){
    const request = await fetch(
      "https://formagency-dc5b4-default-rtdb.firebaseio.com/formdata.json",
      {
        method: "POST", // or 'PUT'
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          fname,
          lname,
          company,
          email,
          message,
        }),
      }
    );
    if (request) {
        setuserdata({
            fname: "",
            lname: "",
            company: "",
            email: "",
            message: "",
        })
        enqueueSnackbar('Submitted')
        
   
    }else{
        enqueueSnackbar(message, { variant: 'error' })
    }
}else{
    enqueueSnackbar("Fill The Form", { variant: 'error' })
}
  };


  
  return (
    <>
      <section className="form_sec">
        <div className="inner_form" id="form">
          <h1>
            Let’s talk<span>.</span>
          </h1>
          <div className="main_form">
            <aside className="Fdetails_aside">
              <h2>Let’s talk about your product</h2>
              <p>
                You can contact us via{" "}
                <a href="mailto:eliaagency548@gmail.com">email</a> or using the
                form
              </p>
              <div className="faside_image">
                <img src={ImageForm} alt="" />
              </div>
            </aside>
            <aside className="form_aside">
              {" "}
              <Box
                component="form"
                sx={{
                  "& > :not(style)": { m: 1, width: "25ch" },
                }}
                noValidate
                autoComplete="off"
              >
                <TextField
                  required
                  id="outlined-basic"
                  label="First name"
                  name="fname"
                  onChange={getValue}
                  value={userdata.fname}
                  variant="outlined"
                />
                <TextField
                  required
                  id="outlined-basic"
                  label="Last name"
                  variant="outlined"
                  name="lname"
                  value={userdata.lname}
                  onChange={getValue}
                />
                <TextField
                  id="outlined-basic"
                  label="Company"
                  variant="outlined"
                  name="company"
                  value={userdata.company}
                  onChange={getValue}
                />
                <TextField
                  required
                  id="outlined-required"
                  label="Email"
                  defaultValue=""
                  type="email"
                  onChange={getValue}
                  name="email"
                  value={userdata.email}
                />
                <p>What are we building?</p>
                <TextField
                  id="outlined-multiline-static"
                  multiline
                  name="message"
                  value={userdata.message}
                  rows={12}
                  onChange={getValue}
                  className="widths_form"
                  placeholder="Share a few details on your gaols, timeline, budget and others."
                />
 <SnackbarProvider  variant="error" />
 <SnackbarProvider  variant="success" />
                <Button
                  className="form_btn"
                  onClick={sendRequest}
                  variant="outlined"
                >
                  Send Request
                </Button>
               
              </Box>
            </aside>
          </div>
        </div>
      </section>
    </>
  );
}

export default Form;
